@import "../../global.scss";


.contact{
  background-color: white;
    display: flex;

    @include mobile{
        flex-direction: column;
    }

    .left{
       flex: 1;
      

     img{
       height: 70%;

         @include mobile{
             width: 100%;
             height: 50%;
         }
    }

       

       h5{
           color: $maincolor ;
           padding-top: 20px;
           text-align: center;
           font-family: cursive;
           font-size: 30px;

           @include mobile{
            font-size: 20px;
           }
       }

       h6{
        font-family: cursive;
        text-align: center;
        font-size: 25px;
        padding-left: 10px;
        padding-top: 20px;

        @include mobile{
            font-size: 15px;
            padding-top: 10px;
           }
       } 

       .imgContainer{
           display: flex;
           justify-content: center;

        img{
            width: 20px;
            height: 20px;
            margin: 20px;
            box-shadow: 3px 3px 5px black;
            transform: scale(1);
            transition: 0.3s ease-in-out;
            
            @include mobile{
                margin: 10px;
            }
        }

        img:hover{
            transform: scale(1.2);
        }
    }
    }
    .right{
        position: relative;
        flex: 1;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        h2{
            font-family: cursive;
            font-weight: bolder;
            font-size: 40px;
            color: $maincolor;
        }

        form{
            width: 70%;
            height: 70%;
            display: flex;
            flex-direction: column;
            align-items: center;
           

            input{
                width: 250px;
                height: 30px;
                font-family: cursive;
                border: none;
                margin: 20px;
                box-shadow: 2px 2px 5px black;

                @include mobile{
                    margin: 10px;
                }
            }

            textarea{
                width: 250px;
                height: 150px;
                font-family: cursive;
                margin: 20px;
                border: none;
                box-shadow: 2px 2px 5px black;

                @include mobile{
                    margin: 10px;
                }
            }

            button{
                width: 100px;
                height: 30px;
                margin: 20px;
                background-color: cornflowerblue ;
                color: white;
                border: none;
                border-radius: 8px;
               font-weight: 500;
               font-family: cursive;
               cursor: pointer;
               box-shadow: 3px 3px 5px black;
               transform: scale(1);
               transition: 0.3s ease-in-out;

               @include mobile{
                margin: 10px;
            }
            }

            button:hover{
                transform: scale(1.2);
            }

            .copyright{
                position: absolute;
                bottom: 5%;
                right: 5%;
                p{
                    font-family: cursive;
                    font-size: 12px;
                    a{
                        text-decoration: none;
                        color: black;
                    }
                }
            }
        }
        
     }
}

